import { AddTicketState } from "../../../store/AppState";
import { makeActionCreatorWithReducerWithPrefix, StateTypeReducer, Action, AnyAction } from "tquinlan1992-typescript-react-redux-utils";

export { makeActionCreatorWithReducerWithPrefix, StateTypeReducer, Action, AnyAction };

export interface SetDescriptionAction {
    value: string;
}

export default makeActionCreatorWithReducerWithPrefix<AddTicketState, SetDescriptionAction>(
    'SET_DESCRIPTION',
    (state, { value }: SetDescriptionAction) => {
        return {
            ...state,
            description: value
        };
    }
);
